<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox">
      <span class="text-base">是否撰写本章节</span>
    </el-checkbox>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="resultsform" :model="form" class="flex flex-wrap pr-4">
      <el-form-item label="基线表" class="w-full pl-4" prop="general_index_file">
        <file-upload :showDownloadBtn="true" ref="resultsUploadRef" templateUrl="/static/一般指标对比.xlsx" @getFile="getGeneralFile"></file-upload>
      </el-form-item>
      <el-form-item label="疗效观察指标表" class="w-full pl-4" prop="efficacy_comparison_file">
        <file-upload :showDownloadBtn="true" ref="resultsMedicalUploadRef" templateUrl="/static/疗效指标对比.xlsx" @getFile="getComparisonFile"></file-upload>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import FileUpload from "@/views/components/FileUpload.vue"

export default {
  components: {
    FileUpload,
  },
  data() {
    return {
      checked: false,
      form: {
        general_index_file: "",
        efficacy_comparison_file: "",
      },
      rules: {
        general_index_file: [{ required: false, message: "请上传基线表", trigger: ["blur", "change"] }],
        efficacy_comparison_file: [{ required: false, message: "请上传疗效观察指标表", trigger: ["blur", "change"] }],
      },
    }
  },
  methods: {
    getGeneralFile(val) {
      this.form.general_index_file = val
    },
    getComparisonFile(val) {
      this.form.efficacy_comparison_file = val
    },
    changeCheckbox(value) {
      this.rules.general_index_file[0].required = value
      this.rules.efficacy_comparison_file[0].required = value
      this.$bus.emit("fromResultsCheckbox", value)
      if (!value) {
        this.$refs["resultsform"].clearValidate()
      }
    },
    formCheck() {
      return new Promise((resolve, reject) => {
        this.$refs["resultsform"].validate((valid) => {
          if (valid) {
            console.log("results submit!")
            resolve(this.form)
          } else {
            console.log("error results sumbit!")
            reject()
          }
        })
      })
    },
  },
}
</script>
