<template>
  <div>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="materialsForm" :model="form" class="flex flex-wrap pr-4">
      <el-form-item label="机构" prop="institution" class="w-2/4 pl-4">
        <el-input v-model="form.institution" placeholder="例如：北京协和医院" maxlength="100" :disabled="!checked" @change="changeInstitution"></el-input>
      </el-form-item>
      <el-form-item label="病例数据时间" prop="case_data_time" class="w-2/4 pl-4">
        <el-input v-model="form.case_data_time" placeholder="例如：2014年1月-2022年6月" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="目标治疗方法（暴露组）" prop="target_treatment_method" class="w-2/4 pl-4">
        <el-input v-model="form.target_treatment_method" placeholder="例如：冷圈套息肉切除术(CSP)" maxlength="100" @change="changeTargetcontrol_treatment_method" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="暴露组病例数" prop="exposed_group_size" class="w-2/4 pl-4">
        <el-input v-model.number="form.exposed_group_size" placeholder="例如：120" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="对照组治疗方法（非暴露组）" prop="control_treatment_method" class="w-2/4 pl-4">
        <el-input v-model="form.control_treatment_method" placeholder="例如：热圈套息肉切除术(HSP)" maxlength="100" :disabled="!checked" @change="changeControlTreatmentMethod"></el-input>
      </el-form-item>
      <el-form-item label="非暴露组病例数" prop="control_group_size" class="w-2/4 pl-4">
        <el-input v-model.number="form.control_group_size" placeholder="例如：110" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="纳入标准" prop="inclusion_criteria" class="w-2/4 pl-4">
        <el-input v-model="form.inclusion_criteria" placeholder="年龄要求：20-65岁，性别要求：无要求，符合：结肠息肉标准" :disabled="!checked" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="数据收集方法" prop="data_collection_methods" class="w-2/4 pl-4">
        <el-input v-model="form.data_collection_methods" placeholder="例如：北京协和医院住院管理系统" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="伦理审查号" prop="ethics_review_number" class="w-2/4 pl-4">
        <el-input v-model="form.ethics_review_number" placeholder="例如：4564364374" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="排除标准" prop="exclusion_criteria" class="w-2/4 pl-4">
        <el-input v-model="form.exclusion_criteria" placeholder="孕期、年龄过大、并发症过多，死亡、高血压、病例中缺失值过多" :disabled="!checked" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="统计工具" prop="statistical_tools" class="w-2/4 pl-4">
        <el-input v-model="form.statistical_tools" placeholder="例如：R语言" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="缺失值处理工具" prop="missing_value_tools" class="w-2/4 pl-4">
        <el-input v-model="form.missing_value_tools" placeholder="例如：Python的sklearn包" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="缺失值处理方法" prop="missing_value_method" class="w-full pl-4">
        <el-input
          v-model="form.missing_value_method"
          placeholder="例如：一般资料年般、临床强标采用均值进行填补;性别采用众数填补。观察指标不允许缺失，即：包含缺失致医的样本直接放弃"
          maxlength="100"
          :disabled="!checked"
        ></el-input>
      </el-form-item>
      <el-form-item label="一般资料" class="w-full pl-4" prop="general_data">
        <list-materials ref="generalDataRef" :checked="checked" @getMaterialsList="getGeneralMaterialsList"></list-materials>
      </el-form-item>
      <el-form-item label="疗效观察指标" class="w-full pl-4" prop="efficacy_observation_indices">
        <list-materials :showAddBtn="true" ref="efficacyObservationIndicesRef" :checked="checked" @getMaterialsList="getEfficacyMaterialsList"></list-materials>
      </el-form-item>
      <el-form-item label="上传参考文献" class="w-full pl-4" prop="file">
        <file-upload ref="materialsUploadRef" desc="上传国标参考文献、用于对临床指标、治疗方法定义说明，1-4篇文献" @getFile="getFile"></file-upload>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { rule } from "postcss"
import ListMaterials from "./ListMaterials.vue"
import FileUpload from "@/views/components/FileUpload.vue"

export default {
  components: {
    ListMaterials,
    FileUpload,
  },
  props: {
    checked: { type: Boolean, default: false },
  },
  data() {
    return {
      form: {
        institution: "",
        case_data_time: "",
        target_treatment_method: "",
        exposed_group_size: "",
        control_treatment_method: "",
        control_group_size: "",
        inclusion_criteria: "年龄要求：20-65岁，性别要求：无要求，符合：结肠息肉标准",
        data_collection_methods: "",
        ethics_review_number: "",
        exclusion_criteria: "孕期、年龄过大、并发症过多，死亡、高血压、病例中缺失值过多",
        statistical_tools: "R语言",
        missing_value_tools: "Python的sklearn包",
        missing_value_method: "一般资料年般、临床强标采用均值进行填补;性别采用众数填补。观察指标不允许缺失，即：包含缺失致医的样本直接放弃",
        general_data: "",
        efficacy_observation_indices: "",
        file: "",
      },
      rules: {
        institution: [{ required: false, message: "请输入机构", trigger: "blur" }],
        case_data_time: [{ required: false, message: "请输入病例数据时间", trigger: "blur" }],
        target_treatment_method: [{ required: false, message: "请输入目标治疗方法（暴露组）", trigger: "blur" }],
        exposed_group_size: [
          { required: false, message: "请输入暴露组病例数", trigger: "blur" },
          { type: "number", message: "暴露组病例数必须为数字值", trigger: "blur" },
        ],
        control_treatment_method: [{ required: false, message: "请输入对照组治疗方法（非暴露组）", trigger: "blur" }],
        control_group_size: [
          { required: false, message: "请输入非暴露组病例数", trigger: "blur" },
          { type: "number", message: "非暴露组病例数必须为数字值", trigger: "blur" },
        ],
        inclusion_criteria: [{ required: false, message: "请输入纳入标准", trigger: "blur" }],
        data_collection_methods: [{ required: false, message: "请输入数据收集方法", trigger: "blur" }],
        ethics_review_number: [{ required: false, message: "请输入伦理审查号", trigger: "blur" }],
        exclusion_criteria: [{ required: false, message: "请输入排除标准", trigger: "blur" }],
        statistical_tools: [{ required: false, message: "请输入统计工具", trigger: "blur" }],
        missing_value_tools: [{ required: false, message: "请输入缺失值处理工具", trigger: "blur" }],
        missing_value_method: [{ required: false, message: "请输入缺失值处理方法", trigger: "blur" }],
        general_data: [{ required: false, message: "请填写一般资料", trigger: ["blur", "change"] }],
        efficacy_observation_indices: [{ required: false, message: "请填写疗效观察指标", trigger: ["blur", "change"] }],
        file: [{ required: false, message: "请上传参考文献", trigger: ["blur", "change"] }],
      },
    }
  },
  methods: {
    resetRequiredForm(value) {
      this.rules.institution[0].required = value
      this.rules.case_data_time[0].required = value
      this.rules.target_treatment_method[0].required = value
      this.rules.exposed_group_size[0].required = value
      this.rules.control_treatment_method[0].required = value
      this.rules.control_group_size[0].required = value
      this.rules.inclusion_criteria[0].required = value
      this.rules.data_collection_methods[0].required = value
      this.rules.ethics_review_number[0].required = value
      this.rules.exclusion_criteria[0].required = value
      this.rules.statistical_tools[0].required = value
      this.rules.missing_value_tools[0].required = value
      this.rules.missing_value_method[0].required = value
      this.rules.general_data[0].required = value
      this.rules.efficacy_observation_indices[0].required = value
      this.rules.file[0].required = value

      if (!value) {
        this.$refs["materialsForm"].clearValidate()
      }
    },
    changeInstitution(value) {
      this.$bus.emit("from_institution", value)
    },
    changeTargetcontrol_treatment_method(value) {
      this.$bus.emit("from_target_treatment_method", value)
    },
    changeControlTreatmentMethod(value) {
      this.$bus.emit("from_control_treatment_method", value)
    },
    getGeneralMaterialsList(val) {
      this.form.general_data = val
    },
    getEfficacyMaterialsList(val) {
      this.form.efficacy_observation_indices = val
    },
    getFile(val) {
      this.form.file = val
    },
    formCheck() {
      return new Promise((resolve, reject) => {
        this.$refs["materialsForm"].validate((valid) => {
          if (valid) {
            console.log("role submit!")
            resolve(this.form)
          } else {
            console.log("error role sumbit!")
            reject()
          }
        })
      })
    },
  },
  created() {
    this.$bus.on("from_institution", (data) => {
      this.form.institution = data
    })
    this.$bus.on("from_target_treatment_method", (data) => {
      this.form.target_treatment_method = data
    })
    this.$bus.on("from_control_treatment_method", (data) => {
      this.form.control_treatment_method = data
    })

    this.$nextTick(() => {
      const defaultGeneralData = [
        {
          id: "人口学资料",
          content: "年龄、性别、民族",
        },
        {
          id: "病史指标",
          content: "无吸烟史、饮酒史",
        },
        {
          id: "临床指标",
          content: "收缩压(SBP)、舒张压(DBP)",
        },
        {
          id: "其他疾病情况",
          content: "是否有高血压、高血脂、高血糖",
        },
      ]
      this.$refs.generalDataRef.list = defaultGeneralData
      this.form.general_data = defaultGeneralData
      const defaultEfficacyObservationIndices = [
        {
          id: "1",
          content: "治疗是否有效",
        },
        {
          id: "2",
          content: "24时是否发生术后并发症",
        },
        {
          id: "3",
          content: "平均住院时间(天)",
        },
        {
          id: "4",
          content: "平物恢复时间(天)",
        },
        {
          id: "5",
          content: "治疗费用",
        },
        {
          id: "6",
          content: "3个月内疾病是否复发",
        },
      ]
      this.$refs.efficacyObservationIndicesRef.list = defaultEfficacyObservationIndices
      this.form.efficacy_observation_indices = defaultEfficacyObservationIndices
    })
  },
}
</script>
