<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox">
      <span class="text-base">是否撰写本章节</span>
    </el-checkbox>
    <form-introduction ref="formIntroductionRef" :checked="checked"></form-introduction>
  </div>
</template>

<script>
import FormIntroduction from "./FormIntroduction.vue"

export default {
  components: {
    FormIntroduction
  },
  data() {
    return {
      checked: false,
    }
  },
  methods: {
    changeCheckbox(value) {
      this.$refs.formIntroductionRef.resetRequiredForm(value)
      this.$bus.emit('fromIntroductionCheckbox', value)
    },
  },
}
</script>
