<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox" :disabled="!disabled">
      <span class="text-base">是否撰写本章节</span>
    </el-checkbox>
    <div class="text-center">
      <el-form :model="form" :rules="rules" ref="abstractForm">
        <el-form-item prop="checkList" class="w-full pl-4">
          <el-checkbox-group v-model="form.checkList" @change="onChange">
            <el-checkbox label="chinese" :disabled="!checked">中文摘要</el-checkbox>
            <el-checkbox label="english" :disabled="!checked">英文摘要</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    disabled() {
      return this.discussionCheckbox && this.introductionCheckbox && this.materialsCheckbox && this.resultsCheckbox
    },
  },
  data() {
    return {
      form: {
        checkList: [],
      },
      rules: {
        checkList: [{ required: true, message: "请至少选择一个摘要", trigger: "change" }],
      },
      checked: false,
      discussionCheckbox: false,
      introductionCheckbox: false,
      materialsCheckbox: false,
      resultsCheckbox: false,
    }
  },
  methods: {
    changeCheckbox(value) {},
    formCheck() {
      return new Promise((resolve, reject) => {
        this.$refs["abstractForm"].validate((valid) => {
          if (valid) {
            console.log("abstract submit!")
            resolve(this.form)
          } else {
            console.log("error abstract sumbit!")
            reject()
          }
        })
      })
    },
    onChange(val){
      console.log('va',val)
    }
  },
  created() {
    this.$bus.on("fromDiscussionCheckbox", (data) => {
      this.discussionCheckbox = data
    })
    this.$bus.on("fromIntroductionCheckbox", (data) => {
      this.introductionCheckbox = data
    })
    this.$bus.on("fromMaterialsCheckbox", (data) => {
      this.materialsCheckbox = data
    })
    this.$bus.on("fromResultsCheckbox", (data) => {
      this.resultsCheckbox = data
    })
  },
}
</script>
