<template>
  <div>
    <el-form label-position="top" label-width="80px" :model="form" :rules="rules" ref="roleForm" class="flex flex-wrap">
      <el-form-item label="我的角色" prop="my_role" class="w-2/4 pl-4">
        <el-input v-model="form.my_role" placeholder="例如：医学消化科研究生3年级的学生" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="AI专家角色" prop="ai_expert_role" class="w-2/4 pl-4">
        <el-input v-model="form.ai_expert_role" placeholder="例如：医学消化科研究生导师" maxlength="100"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        my_role: "",
        ai_expert_role: "",
      },
      rules: {
        my_role: [{ required: true, message: "请输入我的角色", trigger: "blur" }],
        ai_expert_role: [{ required: true, message: "请输入AI专家角色", trigger: "blur" }],
      },
    }
  },
  methods: {
    formCheck() {
      return new Promise((resolve, reject) => {
        this.$refs["roleForm"].validate((valid) => {
          if (valid) {
            console.log("role submit!")
            resolve(this.form)
          } else {
            console.log("error role sumbit!")
            reject()
          }
        })
      })
    },
  },
  created() {},
}
</script>
