<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox">
      <span class="text-base">是否撰写本章节</span>
    </el-checkbox>
    <form-discussion ref="formDiscussionRef" :checked="checked"></form-discussion>
  </div>
</template>

<script>
import FormDiscussion from "./FormDiscussion.vue"

export default {
  components: {
    FormDiscussion,
  },
  data() {
    return {
      checked: false,
    }
  },
  methods: {
    changeCheckbox(value) {
      this.$refs.formDiscussionRef.resetRequiredForm(value)
      this.$bus.emit('fromDiscussionCheckbox', value)
    },
  }
}
</script>
