<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox">
      <span class="text-base">是否撰写本章节</span>
    </el-checkbox>
    <form-materials ref="formMaterialsRef" :checked="checked"></form-materials>
  </div>
</template>

<script>
import FormMaterials from "./FormMaterials.vue"

export default {
  components: {
    FormMaterials,
  },
  data() {
    return {
      checked: false,
    }
  },
  methods: {
    changeCheckbox(value) {
      this.$refs.formMaterialsRef.resetRequiredForm(value)
      this.$bus.emit('fromMaterialsCheckbox', value)
    },
  },
  created() {

  },
}
</script>
