<template>
  <div class="home-page relative min-w-[1200px]">
    <page-header></page-header>
    <main class="w-[1200px] mx-auto">
      <div class="mt-5 pl-2 text-[#aeb9c0]">首页>在线工具><span class="blue-text">AI文献翻译</span></div>
      <section>
        <div class="text-center mt-[60px]">
          <img src="@/assets/pen.png" class="w-[120px]" />
        </div>
        <h1 class="text-center text-[44px] mt-10 mb-8">一分钟做完回顾性队列研究期刊撰写</h1>
        <p class="text-center text-[22px] mb-12">原创出稿，自带查重，AI帮你写作，只需填入内容后自动完成期刊撰写</p>
        <div
          class="shadow-[0_0_14px_4px_rgba(0,0,0,0.1)] flex items-center w-[184px] h-[40px] rounded-full mb-12 mx-auto">
          <img class="h-[24px] pl-5" src="@/assets/pdf.png" />
          <div class="blue-text mx-2">论文模板</div>
          <el-button size="mini" type="info" plain round @click="onView">查看</el-button>
        </div>
        <div class="flex items-center px-8 mb-10">
          <div class="flex items-center border border-solid border-[#b3d8ff] w-1/2 py-4 pl-6 rounded-md cursor-pointer"
            @click="goPage('/topic')">
            <img class="h-[60px]" src="@/assets/topic_tool.png" />
            <div class="ml-6 text-lg">回顾性队列研究选题工具</div>
          </div>
          <div
            class="flex items-center border border-solid border-[#b3d8ff] w-1/2 ml-10 py-4 pl-6 rounded-md cursor-pointer"
            @click="goPage('/supplement')">
            <img class="h-[60px]" src="@/assets/supplement_tool.png" />
            <div class="ml-6 text-lg">回顾性队列研究补证工具</div>
          </div>
        </div>
      </section>
      <section class="shadow-[0_0_14px_4px_rgba(0,0,0,0.1)] pl-4 pr-8 pt-4 pb-10 mb-12 rounded-lg">
        <section-role ref="roleSectionRef"></section-role>
        <el-collapse>
          <el-collapse-item name="1" class="pl-4">
            <template slot="title">
              <span class="font-bold text-base pl-4">引言撰写</span>
            </template>
            <section-introduction ref="introductionSectionRef"></section-introduction>
          </el-collapse-item>
          <el-collapse-item name="2" class="pl-4">
            <template slot="title">
              <span class="font-bold text-base pl-4">资料与方法</span>
            </template>
            <section-materials ref="materialsSectionRef"></section-materials>
          </el-collapse-item>
          <el-collapse-item name="3" class="pl-4">
            <template slot="title">
              <span class="font-bold text-base pl-4">结果撰写</span>
            </template>
            <section-results ref="resultsSectionRef"></section-results>
          </el-collapse-item>
          <el-collapse-item name="4" class="pl-4">
            <template slot="title">
              <span class="font-bold text-base pl-4">讨论撰写</span>
            </template>
            <section-discussion ref="discussionSectionRef"></section-discussion>
          </el-collapse-item>
          <el-collapse-item name="5" class="pl-4">
            <template slot="title">
              <span class="font-bold text-base pl-4">摘要撰写</span>
            </template>
            <section-abstract ref="abstractSectionRef"></section-abstract>
          </el-collapse-item>
        </el-collapse>

        <div class="text-center pl-4 mt-8 mb-10">
          <el-button type="primary" size="medium" class="w-[200px]" @click="onSumbit"
            v-loading.fullscreen.lock="fullscreenLoading">生成论文</el-button>
        </div>
      </section>
    </main>
    <section
      class="w-[1200px] mx-auto mb-12 px-9 border border-dashed border-[#2b323d] flex items-center justify-between h-[150px]"
      v-if="file.name">
      <div class="flex items-center">
        <img class="w-[64px]" src="@/assets/word.png" />
        <div class="flex-1 overflow-hidden ml-3">
          <div class="break-all truncate">{{ file.name }}</div>
          <div class="mt-2 text-xs text-[#999]">{{ bytesToSize(file.size) }}</div>
        </div>
      </div>
      <div>
        <el-button class="w-[150px]" @click="onFileView">预览</el-button>
        <el-button type="primary" class="w-[150px]" @click="onDownload">下载文件</el-button>
      </div>
    </section>
    <page-footer></page-footer>
  </div>
</template>

<script>
import { medicalArticleWriter } from "@/api/home"
import PageHeader from "@/views/components/PageHeader.vue"
import PageFooter from "@/views/components/PageFooter.vue"

import SectionRole from "./components/SectionRole.vue"
import SectionIntroduction from "./components/SectionIntroduction.vue"
import SectionMaterials from "./components/SectionMaterials.vue"
import SectionResults from "./components/SectionResults.vue"
import SectionDiscussion from "./components/SectionDiscussion.vue"
import SectionAbstract from "./components/SectionAbstract.vue"

import { bytesToSize } from "@/utils/validate"

export default {
  components: {
    PageHeader,
    PageFooter,
    SectionRole,
    SectionIntroduction,
    SectionMaterials,
    SectionResults,
    SectionDiscussion,
    SectionAbstract,
  },
  data() {
    return {
      fullscreenLoading: false,
      file: {
        name: "",
        url: "",
        size: "",
      },
    }
  },
  methods: {
    goPage(url) {
      this.$router.push({
        path: url,
      })
    },
    onView() {
      window.open("/static/内镜下冷圈套切除术治疗微小型结直肠息肉疗效评估.pdf")
    },
    bytesToSize(bytes) {
      return bytesToSize(bytes)
    },
    async onSumbit() {
      let flag = true
      let params = {}

      /** 判断-角色-是否填写 **/
      await this.$refs.roleSectionRef
        .formCheck()
        .then((res) => {
          console.log("角色 校验通过", res)
          params.roleSection = res
        })
        .catch((err) => {
          console.log("角色 校验不通过", err)
          this.$message({
            message: "请填写表单内容",
            type: "warning",
          })
          flag = false
        })
      if (!flag) return

      /** 判断-引言撰写-是否勾选 **/
      const introductionChecked = this.$refs.introductionSectionRef.checked
      if (introductionChecked) {
        await this.$refs.introductionSectionRef.$refs.formIntroductionRef
          .formCheck()
          .then((res) => {
            console.log("formIntroduction 校验通过", res)
            params.introductionSection = res
          })
          .catch((err) => {
            console.log("formIntroduction 校验不通过", err)
            this.$message({
              message: "请完善引言撰写的内容",
              type: "warning",
            })
            flag = false
          })
      }
      if (!flag) return

      /** 判断-资料与方法-是否勾选 **/
      const materialsChecked = this.$refs.materialsSectionRef.checked
      if (materialsChecked) {
        await this.$refs.materialsSectionRef.$refs.formMaterialsRef
          .formCheck()
          .then((res) => {
            console.log("formMaterials 校验通过", res)
            params.materialsSection = res
          })
          .catch((err) => {
            console.log("formMaterials 校验不通过", err)
            this.$message({
              message: "请完善资料与方法的内容",
              type: "warning",
            })
            flag = false
          })
      }
      if (!flag) return

      /** 判断-结果撰写-是否勾选 **/
      const resultsChecked = this.$refs.resultsSectionRef.checked
      if (resultsChecked) {
        await this.$refs.resultsSectionRef
          .formCheck()
          .then((res) => {
            console.log("formResults 校验通过", res)
            params.resultsSection = res
          })
          .catch((err) => {
            console.log("formResults 校验不通过", err)
            this.$message({
              message: "请完善结果撰写的内容",
              type: "warning",
            })
            flag = false
          })
      }
      if (!flag) return

      /** 判断-讨论撰写-是否勾选 **/
      const discussionChecked = this.$refs.discussionSectionRef.checked
      if (discussionChecked) {
        await this.$refs.discussionSectionRef.$refs.formDiscussionRef
          .formCheck()
          .then((res) => {
            console.log("formDiscussion 校验通过", res)
            params.discussionSection = res
          })
          .catch((err) => {
            console.log("formDiscussion 校验不通过", err)
            this.$message({
              message: "请完善讨论撰写的内容",
              type: "warning",
            })
            flag = false
          })
      }
      if (!flag) return

      /** 判断-摘要撰写-是否勾选 **/
      const abstractChecked = this.$refs.abstractSectionRef.checked
      if (abstractChecked) {
        await this.$refs.abstractSectionRef
          .formCheck()
          .then((res) => {
            console.log("formAbstract 校验通过", res)
            params.abstractSection = res
          })
          .catch((err) => {
            console.log("formAbstract 校验不通过", err)
            this.$message({
              message: "请完善摘要撰写的内容",
              type: "warning",
            })
            flag = false
          })
      }
      if (!flag) return

      /** 判断是否勾选一个章节 **/
      if (!introductionChecked && !materialsChecked && !resultsChecked && !discussionChecked) {
        this.$message({
          message: "请撰写一个章节再提交！",
          type: "warning",
        })
        return
      }
      this.fullscreenLoading = true
  
      // this.file = {
      //   name: "心外膜消融术临床实际意义是否大于射频消融术",
      //   url: "/static/内镜下冷圈套切除术治疗微小型结直肠息肉疗效评估.pdf",
      //   size: "2344",
      // }
      medicalArticleWriter(params).then((res) => {
        if (res.data) {
          this.file = res.data
        }
        this.fullscreenLoading = false
        console.log("medicalArticleWriter", res)
      })
    },
    onFileView() {
      window.open(this.file.url)
    },
    getFileExtension(filename) {
      return /[.]/.exec(filename) ? /[^.]+$/.exec(filename)[0] : ""
    },
    onDownload() {
      const url = this.file.url
      const fileExtension = this.getFileExtension(url)
      const name = this.file.name + "." + fileExtension
      this.downloadFileFromApi(url, name)
    },
    async downloadFileFromApi(fileUrl, fileName) {
      try {
        const response = await fetch(fileUrl)
        const blob = await response.blob()
        const url = URL.createObjectURL(blob)

        const link = document.createElement("a")
        link.href = url
        link.setAttribute("download", fileName)
        document.body.appendChild(link)
        link.click()

        // 清理工作
        document.body.removeChild(link)
        URL.revokeObjectURL(url)
      } catch (error) {
        console.error("下载模板失败:", error)
      }
    },
  },
  destroyed() {
    this.$bus.off("from_small_disease_background")
    this.$bus.off("from_target_treatment_method")
    this.$bus.off("from_control_treatment_method")
    this.$bus.off("from_specific_disease")
    this.$bus.off("from_innovation")
    this.$bus.off("from_institution")

    this.$bus.off("fromDiscussionCheckbox")
    this.$bus.off("fromIntroductionCheckbox")
    this.$bus.off("fromMaterialsCheckbox")
    this.$bus.off("fromResultsCheckbox")
  },
}
</script>
<style scoped lang="scss">
.home-page {
  background: linear-gradient(to bottom, #ebfafb, #feffff), url("~@/assets/bg.png") no-repeat center top;
}
</style>
<style lang="scss">
.home-page {
  .el-form--label-top .el-form-item__label {
    padding-bottom: 0;
  }

  .el-upload-dragger {
    padding-bottom: 20px;
  }

  .el-upload,
  .el-upload-dragger {
    width: 100%;
    height: auto;
  }

  .el-card {
    overflow: visible;
  }

  .el-collapse {
    border: none;
  }
}
</style>
