<template>
  <div>
    <!-- <div class="pb-3 pt-3"><span class="text-[#F56C6C] mr-1" v-show="isValidate">*</span>{{ title }}</div> -->
    <div class="border border-dashed border-[#cfd2db]">
      <el-row>
        <el-col :span="4">
          <div class="py-4 px-6 font-bold">{{ showAddBtn ? "ID" : "分类" }}</div>
        </el-col>
        <el-col :span="showAddBtn ? 16 : 20">
          <div class="py-4 px-6 font-bold">指标</div>
        </el-col>
        <el-col :span="4" v-if="showAddBtn">
          <div class="py-4 px-6 font-bold">操作</div>
        </el-col>
      </el-row>
      <el-row :class="{ 'bg-[#eff4f5]': i % 2 == 0 }" v-for="(item, i) of list" :key="i">
        <el-col :span="4">
          <div class="py-3 px-6 leading-10">{{ item.id || i + 1 }}</div>
        </el-col>
        <el-col :span="showAddBtn ? 16 : 20">
          <div class="py-3 px-6">
            <el-input v-model="item.content" placeholder="请输入内容" :disabled="!checked" @change="emitMaterialsList()"></el-input>
          </div>
        </el-col>
        <el-col :span="4" v-show="showAddBtn">
          <div class="py-3 px-6 leading-10 text-[#ec6b73] cursor-pointer" :disabled="!checked" @click="onDeleteMaterials(i)">删除</div>
        </el-col>
      </el-row>
    </div>
    <div class="border border-dashed border-[#cfd2db] mt-[30px] py-3 flex items-center justify-center cursor-pointer" v-show="showAddBtn" @click="onAddMaterials">
      <img src="@/assets/add.png" class="h-4 mr-2" />
      添加数据
    </div>
  </div>
</template>

<script>
export default {
  props: {
    showAddBtn: { type: Boolean, default: false },
    checked: { type: Boolean, default: false },
  },
  data() {
    return {
      list: [],
    }
  },
  methods: {
    onDeleteMaterials(i) {
      if (this.list.length > 1) {
        this.list.splice(i, 1)
      } else {
        this.$message.warning("最少需要填写1个" + this.title + "！")
      }
      this.emitMaterialsList()
    },
    onAddMaterials() {
      const id = this.title == "一般资料" ? "指标分类" : ""
      this.list.push({
        id: id,
        content: "",
      })
      this.emitMaterialsList()
    },
    emitMaterialsList() {
      this.$emit("getMaterialsList", this.list)
    },
  },
}
</script>
