<template>
  <div>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="discussForm" :model="form" class="flex flex-wrap pr-4">
      <el-form-item label="小疾病背景" prop="small_disease_background" class="w-2/4 pl-4">
        <el-input v-model="form.small_disease_background" placeholder="例如：结直肠息肉" maxlength="100" @change="changeSmallDiseaseBackground" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="具体研究疾病" prop="specific_disease" class="w-2/4 pl-4">
        <el-input v-model="form.specific_disease" placeholder="例如：微小型结肠息肉" maxlength="100" @change="changeSpecificDisease" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="目标治疗方法（暴露组）" prop="target_treatment_method" class="w-2/4 pl-4">
        <el-input v-model="form.target_treatment_method" placeholder="例如：冷切套息肉切除术(CSP)" maxlength="100" @change="changeTargetcontrol_treatment_method" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="对照组治疗方法（非暴露组）" prop="control_treatment_method" class="w-2/4 pl-4">
        <el-input v-model="form.control_treatment_method" placeholder="例如：热切套息肉切除术(HSP)" maxlength="100" @change="changeControlTreatmentMethod" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="未来研究方向" prop="future_direction" class="w-full pl-4">
        <el-input v-model="form.future_direction" placeholder="例如：患者的术后用药、护理、日常管理：术后生活质量" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="创新点" prop="innovation" class="w-full pl-4">
        <el-input v-model="form.innovation" placeholder="例如：微小型结直肠息肉患者" maxlength="100" @change="changeInnovation" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="疗效对比表" class="w-full pl-4" prop="efficacy_comparison_table">
        <file-upload :showDownloadBtn="true" ref="discussComparisonUploadRef" templateUrl="/static/疗效指标对比.xlsx" @getFile="getFile"></file-upload>
      </el-form-item>
      <el-form-item label="上传参考文献" class="w-full pl-4" prop="discussion_lit_table">
        <file-upload ref="discussFileRef" desc="上传与治疗方法、疾病机理、及评估结果解释相关的文献" countContent="大约 5-10 篇" @getFile="getFile"></file-upload>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import FileUpload from "@/views/components/FileUpload.vue"
export default {
  components: {
    FileUpload,
  },
  props: {
    checked: { type: Boolean, default: false },
  },
  data() {
    return {
      form: {
        small_disease_background: "",
        specific_disease: "",
        target_treatment_method: "",
        control_treatment_method: "",
        future_direction: "患者的术后用药、护理、日常管理：术后生活质量",
        innovation: "微小型结直肠息肉患者",
        efficacy_comparison_table: "",
        discussion_lit_table: "",
      },
      rules: {
        small_disease_background: [{ required: false, message: "请输入小疾病背景", trigger: "blur" }],
        specific_disease: [{ required: false, message: "请输入具体研究疾病", trigger: "blur" }],
        target_treatment_method: [{ required: false, message: "请输入目标治疗方法（暴露组）", trigger: "blur" }],
        control_treatment_method: [{ required: false, message: "请输入对照治疗方法（非暴露组）", trigger: "blur" }],
        future_direction: [{ required: false, message: "请输入未来研究方向", trigger: "blur" }],
        innovation: [{ required: false, message: "请输入创新点", trigger: "blur" }],
        efficacy_comparison_table: [{ required: false, message: "请上传疗效对比表", trigger: "blur" }],
        discussion_lit_table: [{ required: false, message: "请上传参考文献", trigger: "blur" }],
      },
    }
  },
  methods: {
    resetRequiredForm(value) {
      this.rules.small_disease_background[0].required = value
      this.rules.specific_disease[0].required = value
      this.rules.target_treatment_method[0].required = value
      this.rules.control_treatment_method[0].required = value
      this.rules.future_direction[0].required = value
      this.rules.innovation[0].required = value
      this.rules.efficacy_comparison_table[0].required = value
      this.rules.discussion_lit_table[0].required = value

      if (!value) {
        this.$refs["discussForm"].clearValidate()
      }
    },
    changeSmallDiseaseBackground(value) {
      this.$bus.emit("from_small_disease_background", value)
    },
    changeTargetcontrol_treatment_method(value) {
      this.$bus.emit("from_target_treatment_method", value)
    },
    changeControlTreatmentMethod(value) {
      this.$bus.emit("from_control_treatment_method", value)
    },
    changeSpecificDisease(value) {
      this.$bus.emit("from_specific_disease", value)
    },
    changeInnovation(value) {
      this.$bus.emit("from_innovation", value)
    },
    formCheck() {
      return new Promise((resolve, reject) => {
        this.$refs["discussForm"].validate((valid) => {
          if (valid) {
            console.log("role submit!")
            resolve(this.form)
          } else {
            console.log("error role sumbit!")
            reject()
          }
        })
      })
    },
    getFile(value) {
      this.form.file = value
    },
  },
  created() {
    this.$bus.on("from_small_disease_background", (data) => {
      this.form.small_disease_background = data
    })
    this.$bus.on("from_target_treatment_method", (data) => {
      this.form.target_treatment_method = data
    })
    this.$bus.on("from_control_treatment_method", (data) => {
      this.form.control_treatment_method = data
    })
    this.$bus.on("from_specific_disease", (data) => {
      this.form.specific_disease = data
    })
    this.$bus.on("from_innovation", (data) => {
      this.form.innovation = data
    })
  },
}
</script>
